.footer {
  background-color: $secondBgColor;
}

.footer__block {
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 767px) {
    height: 100px;
  }

  @media screen and (min-width: 768px) {
    height: 77px;
  }
}

.footer-item {
  font-size: 16px;
  line-height: 1.19;
  color: $footerTextColor;

  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  @media screen and (max-width: 767px) {
    font-size: 14px;
    line-height: 1.14;
    flex-wrap: wrap;
    word-break: break-all;
  }
}
.footer__image-heart {
  width: 14px;
  height: 13px;
  margin-right: 10px;
  margin-left: 10px;
}

.item-li {
  @media screen and (max-width: 767px) {
    display: flex;
    flex-wrap: wrap;
    margin-right: 26px;
    margin-left: 26px;
  }
}

.style-link {
  list-style-type: none;
  color: $footerTextColor;
  text-decoration: underline;
}

