.popup {
  position: fixed;
  // width: 100%;
  // height: 100%;
  background-color: rgba(14, 11, 11, 0.8);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s;
  z-index: 1000;
  @media screen and (max-width: 767px) {
    min-height: 100%;
    width: 320px;
  }

  // @media screen and (min-width: 1024px) {
  //   width: 320px;
  // }
}
.popup_body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_content {
  background-color: rgb(245, 250, 249);
  color: $mainTextColor;
  max-width: 800px;
  padding: 25px;
  position: relative;
  transition: all 0.8s ease 0s;
  opacity: 0;

  transform: perspective(600px) translate(0px, -100%) rotate(45deg);
  border: solid 2px $accentColor;
  border-radius: 10px;
  background-image: url('../images/zzz.jpg');
  @media screen and (max-width: 767px) {
    min-height: 100%;
    width: 280px;
  }

  @media screen and (min-width: 768px) {
    min-height: 100%;
    width: 280px;
  }

  @media screen and (min-width: 1024px) {
    width: 320px;
  }
}

.text {
  @media screen and (max-width: 767px) {
    width: 1124px;
  }
}

.info {
  color: $mainBgColor;
  margin: 0px 10px 1em 0px;
  text-align: center;
}
.popup-link:hover,
.popup-link:focus {
  color: $accentColor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup_content {
  transform: perspective(600px) translate(0px, 0%) rotate(0deg);
  opacity: 1;
}
