.popup_title {
  font-size: 20px;
  margin: 20px 20px 1em 20px;
  color: $footerTextColor;
  text-align: center;
}

.popup_text {
  color: $accentColor;

  font-size: 20px;
  margin: 0px 0px 1em 0 px;
  list-style-type: none;
  @media screen and (max-width: 767px) {
    font-size: 14px;
  }
}
.popup-proff {
  font-size: 15px;
  line-height: 1.14;
  text-align: center;

  margin: 10px 10px 1em 10px;
}
.popup-skills {
  font-size: 20px;
  margin: 10px 10px 1em 10px;
  text-align: center;
}
.popup-skills_link {
  text-align: center;
  font-size: 20px;
  margin: 10px 10px 1em 10px;
}

.popup-github__link,
.popup-instagram__link,
.popup-facebook__link,
.popup-telegram__link {
  color: $mainBgColor;
  fill: currentColor;
}
.github,
.instagram,
.facebook,
.reply,
.telegram {
  width: 20px;
  height: 20px;
  margin: 10px 10px 10px 10px;
}
.popup-icon_reply {
  text-align: center;
  color: $errorTextColor;
  fill: currentColor;
}

.popup-icon_reply:hover,
.popup-icon_reply:focus {
  color: $accentColor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.popup_social {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;

  color: $footerTextColor;
}

.popup-link__mail,
.popup-link__tel {
  font-style: normal;
  font-size: 15px;
  line-height: 1.17;
  color: $footerTextColor;
  margin-bottom: 8px;
  text-align: center;
}

.popup-link__mail:hover,
.popup-link__mail:focus,
.popup-link__tel:hover,
.popup-link__tel:focus {
  color: $accentColor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.popup-github__link:hover,
.popup-instagram__link:hover,
.popup-facebook__link:hover,
.popup-telegram__link:hover,
.popup-github__link:focus,
.popup-instagram__link:focus,
.popup-facebook__link:focus,
.popup-telegram__link:focus {
  color: $accentColor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

