.popup_content {
  margin: 80px auto;
  width: 320px;
}

.popup_close {
  background-image: -webkit-linear-gradient(top, #f4f1ee, #fff);
  background-image: linear-gradient(top, #f4f1ee, #fff);
  border-radius: 50%;
  box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.3), inset 0px 4px 1px 1px white,
    inset 0px -3px 1px 1px rgba(204, 198, 197, 0.5);
  float: left;
  height: 25px;
  margin: 0px 0px 0px 220px;
  position: relative;
  width: 25px;
  -webkit-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

.popup_close:after {
  color: #000;
  content: '';
  display: block;
  font-size: 15px;
  height: 30px;
  text-decoration: none;
  text-shadow: 0px -1px 1px #bdb5b4, 1px 1px 1px white;
  position: absolute;
  width: 30px;
}

.cross:after {
  content: 'âœ–';
  left: 6.5px;
  top: 5px;
}

.popup_close:hover {
  background-image: -webkit-linear-gradient(top, #fff, #f4f1ee);
  background-image: linear-gradient(top, #fff, #f4f1ee);
  color: $accentColor;
}

.cross:hover:after,
.cross:focus:after {
  color: $accentColor;
  text-shadow: 0px 0px 6px $accentColor;
}

.popup_close:active {
  background-image: -webkit-linear-gradient(top, #efedec, #f7f4f4);
  background-image: linear-gradient(top, #efedec, #f7f4f4);
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.4), inset 0px -3px 1px 1px rgba(204, 198, 197, 0.5);
}

.popup_close:active:after {
  color: #dbd2d2;
  text-shadow: 0px -1px 1px #bdb5b4, 0px 1px 1px white;
}

